        @import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

        body {margin: 0; font-family: 'maven pro';}
        .wrapper {max-width: 1440px; margin: auto; position: relative;padding: 40px 25px;}
        .section {position: relative;}
        figure {margin: 0;}
        :root {
            --color-primary: #6B6B3B;
            --color-secondary: #F6EE34;
            --color-tertiary: #D8D4A3;
            --text-header-style: #2c2c2c;
            --text-paragraph-style: #ffffff;
        }
        h1,h2,h3,h4,p{margin: 0;}

        input, textarea, button {box-sizing: border-box;}

        #section-services {}
        #section-services .container{}
        #section-services .info{}
        #section-services .info h2{font-size: 50px;color: var(--color-primary);margin: 0 0 48px 85px; font-weight: normal;}
        #section-services .boxes{display: flex;gap: 21px;justify-content: space-between;align-items: center;}
        #section-services .boxes section{background:#fff; position: relative;display: flex;flex-direction: column;justify-content: center;align-items: center;width: 100%; transition: .6s;}
        #section-services .boxes section figure {border: 1px solid #889633;}
        #section-services .boxes section figure img {display: block; max-width: 100%;}
        #section-services .boxes section figcaption {background: var(--color-primary); padding: 16px 10px;}
        #section-services .boxes section figcaption p{font-size: 25px; color: #fff; text-align: center;}
        #section-services .boxes section a {position: absolute;width: 100%; height: 100%; left: 0; top: 0; display: block;}
        @media (max-width: 1110px) {
            #section-services .boxes section figcaption {
                padding: 0px 10px;
                min-height: 70px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
        @media (max-width: 1200px) {
            #section-services .boxes section p {font-size: 28px;}
        }
        @media (max-width: 1024px) {
            #section-services .boxes {flex-wrap: wrap;justify-content: space-evenly;}
            #section-services .boxes section {width: auto;}
            #section-services .info h2 {margin-left: 0;text-align: center;}
        }
        @media (max-width: 480px) {
        }